import React from 'react';
import {Card, CardImg, CardText, CardTitle, CardBody, CardSubtitle} from 'reactstrap'
import {Loading} from "./LoadingComponent";
import { baseUrl } from "../shared/baseUrl";
import { FadeTransform } from 'react-animation-components'


function RenderCard({item, isLoading, errMess}) {

    if (isLoading) {
        return (
            <Loading />
        );
    }

    else if (errMess) {
        return (
            <h4>{errMess}</h4>
        );
    }

    else
        return (
            <FadeTransform in
                transformProps={{
                        exitTransform: 'scale(0.5) translateY(-50%)'
            }}>
                <Card>
                    <CardImg src={baseUrl + item.image} alt={item.name}/>
                    <CardBody>
                        <CardTitle style={{fontWeight: 'bold'}}>{item.name}</CardTitle>
                        {item.designation ? <CardSubtitle style={{fontWeight: 'bold'}}>{item.designation}</CardSubtitle> : null}
                        <CardText>
                            {item.description}
                        </CardText>
                    </CardBody>
                </Card>
            </FadeTransform>
        );
}

function Home(props) {
    return (
        <div className="container">
            <div className="row align-items-start">
                <div className="col-12 col-md m-1">
                    <RenderCard item={props.dish}
                                isLoading={props.dishesLoading}
                                errMess={props.dishesErrMess}
                    />
                </div>
                <div className="col-12 col-md m-1">
                    <RenderCard item={props.promotions}
                                isLoading={props.promosLoading}
                                errMess={props.promosErrMess}

                    />
                </div>
                <div className="col-12 col-md m-1">
                    <RenderCard item={props.leaders}
                                isLoading={props.leadersLoading}
                                errMess={props.leadersErrMess}/>
                </div>
            </div>
        </div>
    );
}

export default Home;


